<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>院部管理</el-breadcrumb-item>
      <el-breadcrumb-item>教研室列表列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="教研室名" prop="staffroomName">
              <el-input
                v-model="queryVo.staffroomName"
                @keyup.enter.native="queryPage"
                placeholder="请输入教研室室名称"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="负责教师" prop="teaName">
              <el-input
                v-model="queryVo.teaName"
                @keyup.enter.native="queryPage"
                placeholder="请输入负责教师名称"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                icon="el-icon-search"
                @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          align="center"
          prop="staffroomName"
          label="教研室名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="staffroomId"
          label="教研室id"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="staffroomSetupTime"
          label="教研室设立时间"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="staffroomPosition"
          label="教研室位置"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="staffrommDemo"
          label="教研室介绍"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="teaName"
          label="教研室主任"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptId"
          label="部门id"
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="教研室名称" prop="staffroomName">
                <el-input
                  v-model="Form.staffroomName"
                  placeholder="请输入教研室名称"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="教研室id" prop="staffroomId">
                <el-input
                  v-model="Form.staffroomId"
                  placeholder="请输入教研室id"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="设立时间" prop="staffroomSetupTime">
            <el-input
              v-model="Form.staffroomSetupTime"
              placeholder="请输入设立时间"
            ></el-input>
          </el-form-item>
          <el-form-item label="教研室位置" prop="staffroomPosition">
            <el-input
              v-model="Form.staffroomPosition"
              placeholder="请输入教研室位置"
            ></el-input>
          </el-form-item>
          <el-form-item label="教研室介绍" prop="staffrommDemo">
            <el-input
              v-model="Form.staffrommDemo"
              placeholder="请输入教研室介绍"
            ></el-input>
          </el-form-item>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="教研室主任" prop="teaId">
                <el-select v-model="Form.teaId" filterable placeholder="请输入内容">
                  <el-option
                    v-for="item in teaOptions"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="所属部门" prop="deptId">
                <el-select
                  v-model="Form.deptId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in deptOptions"
                    :key="item.id"
                    :label="item.deptName"
                    :value="item.deptId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      // idOptions: [],
      ids: [],
      staffroomIds: [],
      Form: {},
      rules: {
        staffroomName: [
          { required: true, message: '教研室名称不能为空', trigger: 'blur' }
        ],
        deptId: [
          { required: true, message: '部门名称不能为空', trigger: 'blur' }
        ],
        teaId: [
          { required: true, message: '教研室主任不能为空', trigger: 'blur' }
        ],
        staffroomId: [
          { required: true, message: '教研室id不能为空', trigger: 'blur' }
        ]
      },
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        staffroomName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      entity: {},
      open: false,
      teaOptions: [],
      deptOptions:[],
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {

    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/teaBaseInfo/queryTeaBaseInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: res1 } = await this.$http.post(
        '/deptInfo/queryDeptInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      // console.log(res.data.dataList.records)
      // console.log(res1.data.dataList.records)
      this.teaOptions = res.data.dataList.records
      this.deptOptions = res1.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
    },

    unique(arr) {
    const res = new Map();
    return arr.filter((arr) => !res.has(arr.deptName) && res.set(arr.deptName, 1));
},
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/staffRoomInfo/saveOrUpdateStaffRoomInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/staffRoomInfo/getByIdStaffRoomInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },

    del(row) {
      if (row.staffroomId) {
        var a = row.staffroomId.split(',')
      }
      const userid = a || this.staffroomIds
      // console.log(typeof(userid) );
      // console.log(userid);

      this.$confirm('确认要删除 id 为 ' + userid + ' 的数据项吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/staffRoomInfo/removeByIdStaffRoomInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },

    async queryPage() {
      const { data: res } = await this.$http.post(
        '/staffRoomInfo/queryStaffRoomInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      const datalist = res.data.dataList
      if (res.code != 20000) return this.$message.error(res.message)

      // this.idOptions = datalist.records

      this.List = datalist.records
      this.page.total = datalist.total
      this.page.current = datalist.current
      this.page.size = datalist.size
      // console.log(this.List);
    },
    //选择框
    handleSelectionChange(selection) {
      // console.log(selection)
      this.ids = selection.map(item => item.id)
      this.staffroomIds = selection.map(item => item.staffroomId)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
